import React from 'react';
import {
    Card, CardBody, CardTitle, Row
} from 'reactstrap';
import HashLoader from 'react-spinners/HashLoader';

const Loading = ({label}) => (
    <div style={{padding: '30px'}}>
        <Card style={{zIndex: '999999'}}>
            <CardBody>
                <CardTitle>
                    <Row style={{justifyContent: 'center'}}>
                        <HashLoader
                            size={50}
                            color="#26c6da"
                            loading
                        />
                    </Row>
                    <Row style={{justifyContent: 'center'}}>
                        {label}
                    </Row>
                </CardTitle>
            </CardBody>
        </Card>
    </div>

);


export default Loading;
