import { put, select } from 'redux-saga/effects';
import {PAGE_CHANGE_ERROR, receiveSession} from "../actions/common";

export default function* pageChangeRequest({page}) {
    try {
            let session = yield select(state => state.SESSION)
            console.log("session", session)
            console.log(page)
            session.profile.page = page
            yield put(receiveSession(session.profile, session.token))
    } catch (err) {
        yield put({type: PAGE_CHANGE_ERROR});
    }
}
