import {UMODULE_FAILED_TABLE, UMODULE_REQUEST_TABLE, UMODULE_SUCCESS_TABLE} from "../actions/uModule";


export default function UMODULETABLE(state =
                                    {success: false, sending: false,
                                        error: false},action) {
    switch (action.type) {
        case UMODULE_REQUEST_TABLE:
            return {
                ...state, sending: true, error: false, success: false
            };
        case UMODULE_SUCCESS_TABLE:
            return {
                ...state, success: true, sending: false, error: false, payload: action.payload
            };
        case UMODULE_FAILED_TABLE:
            return {
                ...state, success: false,  sending: false, error: true
            }
        default:
            return state;
    }
}
