import {
    SESSION_RECEIVED,
} from '../actions/common';

export default function SESSION(state = {loading: false, requested: false}, action) {
    switch (action.type) {
        case SESSION_RECEIVED:
            return {
                ...state,
                profile: action.profile,
                token: action.token,
                loading: false,
            };
        default:
            return state;
    }
}
