import React, {useState} from 'react';
import {connect} from "react-redux";
import {loginRequest} from "../../store/actions/common";
import logo from "../../images/img.png"
function Login({loginRequested, loginError}) {
    const [email,setUsername] = useState('');
    const [password,setPassword] = useState('')

    const callHandlerLogin = (e) =>{
        e.preventDefault()
        loginRequested(email,password)
    }
    const setPass = (event) =>{
        setPassword(event.target.value);
    }
    const setUser = (event) =>{
        setUsername(event.target.value)
    }
    return (
        <div className="login bg-dark">
            <form  className="container-login" onSubmit={(e) => callHandlerLogin(e)}>
                <div><img width={163} height={100} src={logo} alt="TheClowny"/></div>
                <div className="container-input">
                    <input className="input-text fw-bold" type="text" id="email" required onChange={e => setUser(e)}/>
                    <span className="floating-label">Email</span>
                </div>
                <div  className="container-input">
                    <input className="input-text fw-bold" type="password" id="password" required onChange={e => setPass(e)}/>
                    <span className="floating-label">Password</span>
                </div>


                <button className="button-gradient fw-bold" type ="submit">Login</button>
            </form>
            {loginError && (
                <div className="container-error">
                    Password or username incorrect!
                </div>
            )}
        </div>
    );
}

export default connect(
    state => ({
        loginError: state.LOGIN.error
    }),
    dispatch =>({
        loginRequested: (email,password) => dispatch(loginRequest(email,password))
    })
)(Login);
