import {LOGIN_FAILED, LOGIN_PENDING, LOGIN_REQUEST, LOGIN_SUCCESS} from "../actions/common";

export default function LOGIN(state =
                                  {logged: false, sending: false,
                                      error: false},action) {
    switch (action.type) {
        case LOGIN_REQUEST:
            return {
                ...state, sending: true, error: false
            };
        case LOGIN_SUCCESS:
            return {
                ...state, logged: true, sending: false, error: false
            };
        case LOGIN_PENDING:
            return {
                ...state, logged: false,  sending: true, error: false
            }
        case LOGIN_FAILED:
            return {
                ...state, logged: false,  sending: false, error: true
            }
        default:
            return state;
    }
}
